import React, { useState, useEffect } from 'react';
import { Button, ButtonGroup, Container } from 'react-bootstrap';
import axiosRetry from 'axios-retry';
import axios from 'axios';

import { Separator, RadioDD, CheckboxDD, SearchCheckboxDD } from '../../components/customDropdown';
import { FilterContainer, FilterModal } from '../../components/filter';
import Loader from '../../components/loader/Loader';
import Comment from '../../components/comment/Comment';
import { InternalServerError, ResourceNotFoundError } from '../../components/error/Error';
import { optionRegion, optionMetro, optionRange, optionVertical, optionGroupCombo1, EMPTY_INITIAL } from '../../constants/filterOptions';
import { API_BASE_URL } from '../../constants/config';
import { comments } from '../../constants/comments';

import OverviewContentType1 from './OverviewContentType1';
import OverviewContentType2 from './OverviewContentType2';


const Overview = ({activeTab, setActiveTab}) => {
	const [grossData, setGrossData] = useState([]);
	const [pagData, setPAGData] = useState([]);
	const [netData, setNetData] = useState([]);
	const [isLoading, setIsLoading] = useState(true)
	

	// show all filters
	const [showFilter, setShowFilter] = useState(false);
	const onShowFilter = () => {
		setShowFilter(true)
	}
	const onCloseFilter = async () => {
		await onCancelRegion();
		await onCancelMetro();
		await onCancelRange();
		await onCancelVertical();
		await onCancelGroup1();
		setShowFilter(false);
	}
	const onApplyFilter = async () => {
		await onApplyRegion();
		await onApplyMetro();
		await onApplyRange();
		await onApplyVertical();
		await onApplyGroup1();
		setShowFilter(false);
	}

	// comment area
	const [showComment, setShowComment] = useState(false);
	const onCloseComment = () => {
		setShowComment(false);
	}
	const onShowComment = () => {
		setShowComment(true)
	};

	// checkbox for region values
	const [region_val_onApply, set_region_val_onApply] = useState([]);
	const [region_val, set_region_val] = useState([]);
	const onApplyRegion = () => {
		set_region_val_onApply(region_val);
	};
	const onCancelRegion = () => {
		set_region_val(region_val_onApply);
	};

	// searchCheckbox for metro values
	const [metro_val_onApply, set_metro_val_onApply] = useState([]);
	const [metro_val, set_metro_val] = useState([]);
	const onApplyMetro = () => {
		set_metro_val_onApply(metro_val);
	};
	const onCancelMetro = () => {
		set_metro_val(metro_val_onApply);
	};

	// radio for range values
	const range_initial_value = optionRange ? optionRange[0].key : EMPTY_INITIAL;
	const [range_val_onApply, set_range_val_onApply] = useState(range_initial_value);
	const [range_val, set_range_val] = useState(range_initial_value);
	const onApplyRange = () => {
		set_range_val_onApply(range_val);
	};
	const onCancelRange = () => {
		set_range_val(range_val_onApply);
	};

	// radio for vertical values
	const vertical_initial_value = optionVertical ? optionVertical[0].key : EMPTY_INITIAL;
	const [vertical_val_onApply, set_vertical_val_onApply] = useState(vertical_initial_value);
	const [vertical_val, set_vertical_val] = useState(vertical_initial_value);
	const onApplyVertical = () => {
		set_vertical_val_onApply(vertical_val)
	};
	const onCancelVertical = () => {
		set_vertical_val(vertical_val_onApply)
	};

	// radio for group1 values
	const group1_initial_value = optionGroupCombo1 ? optionGroupCombo1[0].key : EMPTY_INITIAL;
	const [group1_val_onApply, set_group1_val_onApply] = useState(group1_initial_value);
	const [group1_val, set_group1_val] = useState(group1_initial_value);
	const onApplyGroup1 = () => {
		set_group1_val_onApply(group1_val);
	};
	const onCancelGroup1 = () => {
		set_group1_val(group1_val_onApply);
	};

	// Axios call
	const fetchData = async () => {
		let url = `${API_BASE_URL}/bookings/overview.json?print=pretty`;
		// Uncomment only when API is now available and working
		// const token = localStorage.getItem('token');
		// TODO: Add url and defaultQueryParameters to call Filter API calls

		setIsLoading(true);

		axiosRetry(axios, { retries: 3 });

		await axios({
			method: 'GET',
			url: url
			// Uncomment only when API is now available and working
			// url: url + defaultQueryParameters,
			// headers: {
			//   'Content-Type': 'application/json',
			//   "Authorization": `bearer ${token}`
			// }
		}).then(result => {
			let data = result.data;
			// Uncomment only if for API testing purposes
			// console.log(result.config.url);
			setGrossData(data['gross']);
			setPAGData(data['pag']);
			setNetData(data['net']);

			setIsLoading(false);
		}).catch(error => {
			console.log(error);
			setGrossData([]);
			setPAGData([]);
			setNetData([]);
			setIsLoading(false);
		});
	};



	useEffect(() => {
		fetchData();
	},[]
		// Uncomment only when API is now available and working
		// [
		//   region_val_onApply,
		//   metro_val_onApply,
		//   measure_val_onApply,
		//   range_val_onApply
		// ]
	);

	if (isLoading) return <Loader withContainer />;

	return (
		<>
			<div className={`page-content ${showComment === true ? 'open' : 'closed'}`}>
				{(grossData || pagData || netData) ?
						(grossData.segment_details.length > 0 || pagData.net_percent_of_gross_by_segment.length > 0 || netData.net_percent_of_gross_by_segment.length > 0) ?
							<>
								<FilterContainer
									showComment={showComment}
									onShowFilter={onShowFilter}
									onShowComment={onShowComment}
								>
									<div className="filters">
										<ButtonGroup className="sub-tab">
											<Button
												variant='light'
												className={activeTab === 'gross' ? 'active' : ''}
												onClick={() => { setActiveTab('gross') }}>
												Gross Bookings
                			</Button>
											<Button
												variant='light'
												className={activeTab === 'pag' ? 'active' : ''}
												onClick={() => { setActiveTab('pag') }}>
												PAG
                			</Button>
											<Button
												variant='light'
												className={activeTab === 'net' ? 'active' : ''}
												onClick={() => { setActiveTab('net') }}>
												Net Bookings
											</Button>
										</ButtonGroup>
										<Separator height='39px' />
										<CheckboxDD
											title="Region"
											id="region"
											options={optionRegion}
											valueApplied={region_val_onApply}
											checkVal={region_val}
											setCheckVal={set_region_val}
											onApply={onApplyRegion}
											onCancel={onCancelRegion}
										/>
										<SearchCheckboxDD
											title="Metro"
											id="metro"
											options={optionMetro}
											valueApplied={metro_val_onApply}
											checkVal={metro_val}
											setCheckVal={set_metro_val}
											onApply={onApplyMetro}
											onCancel={onCancelMetro}
										/>
										<RadioDD
											title="Range by"
											id="range"
											options={optionRange}
											radioVal={range_val}
											setRadioVal={set_range_val}
											onApply={onApplyRange}
											onCancel={onCancelRange}
											initialValue={range_initial_value}
										/>
										<RadioDD
											id="vertical"
											options={optionVertical}
											radioVal={vertical_val}
											setRadioVal={set_vertical_val}
											onApply={onApplyVertical}
											onCancel={onCancelVertical}
											initialValue={vertical_initial_value}
										/>
										<RadioDD
											title="Group by"
											id="group"
											options={optionGroupCombo1}
											radioVal={group1_val}
											setRadioVal={set_group1_val}
											onApply={onApplyGroup1}
											onCancel={onCancelGroup1}
											initialValue={group1_initial_value}
										/>
									</div>
									<FilterModal
										title="Navigator"
										setShowFilter={setShowFilter}
										showFilter={showFilter}
										onCloseFilter={onCloseFilter}
										onApplyFilter={onApplyFilter}
									>
										<CheckboxDD
											title="Region"
											id="region-modal"
											visibleInModal
											options={optionRegion}
											checkVal={region_val}
											setCheckVal={set_region_val}
										/>
										<SearchCheckboxDD
											title="Metro"
											id="metro-modal"
											visibleInModal
											options={optionMetro}
											checkVal={metro_val}
											setCheckVal={set_metro_val}
										/>
										<RadioDD
											title="Range by"
											id="range-modal"
											visibleInModal
											options={optionRange}
											radioVal={range_val}
											setRadioVal={set_range_val}
										/>
										<RadioDD
											title="Vertical"
											id="vertical-modal"
											visibleInModal
											options={optionVertical}
											radioVal={vertical_val}
											setRadioVal={set_vertical_val}
										/>
										<RadioDD
											title="Group by"
											id="group-modal"
											visibleInModal
											options={optionGroupCombo1}
											radioVal={group1_val}
											setRadioVal={set_group1_val}
										/>
									</FilterModal>
								</FilterContainer>
								{/* ============= Your Component here! ============= */}
								{<Container fluid style={{ display:`${activeTab === 'gross' ? 'block': 'none'}` }}>
									<OverviewContentType1 apiData={grossData} tab_name='GROSS BOOKINGS' tab_id="gross-overview"/>
								</Container>}
								{<Container fluid style={{ display:`${activeTab === 'pag' ? 'block': 'none' }` }}>
									<OverviewContentType2 apiData={pagData} apiData2={grossData} tab_name='PAG' tab_id="pag-overview"/>
								</Container>}
								{<Container fluid style={{ display:`${activeTab === 'net' ? 'block': 'none' }` }}>
									<OverviewContentType2 apiData={netData} apiData2={grossData} tab_name='NET BOOKINGS' tab_id="net-overview"/>
								</Container>}
							</>
							: <ResourceNotFoundError redirectURL={"/executive-cockpit/bookings/overview"} />
						: <InternalServerError redirectURL={"/executive-cockpit/bookings/overview"} />
				}
			</div>

			<Comment
				data={comments}
				onCloseComment={onCloseComment}
				showComment={showComment}
			/>
		</>
	);
};

export default Overview;
