import React, { useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";


import Legend from '../../components/legend/Legend';
import BasicDD from '../../components/customDropdown/BasicDD';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import LineChart from '../../components/chart/LineChart';
import WaterFallChart from '../../components/chart/WaterfallChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { EMPTY_INITIAL, optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';

import OverviewContentType1 from './OverviewContentType1';


const OverviewContentType2 = (props) => {
	const { apiData, apiData2, selectedRangeValue, tab_id, tab_name } = props;
	const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0].key : EMPTY_INITIAL;
	const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

	const overallVertical = apiData ? apiData.overall : [];
	const grossNetBkgBrideBySegment = apiData ? apiData.gross_to_net_bkg_bridge_by_segment : [];
	const grossNetBkgBridgeTrends = apiData ? apiData.gross_net_bkg_bridge_trends : [];
	const netPercentOfGrossBySegment = apiData ? apiData.net_percent_of_gross_by_segment : [];
	const netPercentOfGrossTrends = apiData ? apiData.net_percent_of_gross_trends : [];
	const grossNetBkgWaterfall = apiData ? apiData.gross_net_bkg_waterfall : [];

	const indicatorPercent = (cell, row) => {
		let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
		let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

		return (
			<>
				<span className={`indicator ${bgColor}`}>
					{icon}
					{convertToPercent(cell)}
				</span>
			</>
		);
	};

	const formatUSDGrowth = (cell, row) => {
		return (
			<span>
				{convertToUSDGrowth(cell)}
			</span>
		);
	};

	const defaultSorted = [
		{
			dataField: 'mix_percent',
			order: 'desc', // or asc
		}
	];

	const columns = [
		{
			dataField: '_id',
			text: 'id',
			hidden: true
		},
		{
			dataField: 'segment',
			text: 'SEGMENT',
			align: 'left',
			headerAlign: 'left',
			classes: 'segment-th',
			headerClasses: 'segment-td'
		},
		{
			dataField: 'net_as_percent_of_gross',
			text: 'NET AS % OF GROWTH',
			sort: true,
			formatter: formatUSDGrowth,
			align: 'right',
			headerAlign: 'right'
		},
		{
			dataField: 'q_to_q_growth',
			text: 'Q-Q GROWTH',
			sort: true,
			formatter: indicatorPercent,
			align: 'center',
			headerAlign: 'center'
		},
		{
			dataField: 'q_by_q_growth_percent',
			text: 'P4QGROWTH',
			sort: true,
			formatter: indicatorPercent,
			align: 'center',
			headerAlign: 'center'
		},
		{
			dataField: 'y_to_y_growth',
			text: 'Q-Q GROWTH',
			sort: true,
			formatter: indicatorPercent,
			align: 'center',
			headerAlign: 'center'
		}
	];
	return (
			<>
				<OverviewContentType1 apiData={apiData2} tab_name={tab_name} tab_id={tab_id}/>
				<Row className="contentDivider" style={{ paddingTop: "30px", paddingBottom: "30px" }}>
					<Col style={{ maxWidth: "400px" }}>
						<Card className="revenue" >
							<Card.Body>
								<Card.Title>gross-net bkg bridge</Card.Title>
								<KPI_NEW
									totalValue={overallVertical.total.value}
									totalUnit={overallVertical.total.unit}
									details={overallVertical.details}
								/>
							</Card.Body>
						</Card>
					</Col>
					<Col>
						<Card className="revenue">
							<Card.Body>
								<Card.Title>gross-net bkg bridge trends</Card.Title>
								<div className="d-flex">
									<Legend legend={grossNetBkgBrideBySegment.legend} />
									{selectedRangeValue === "last_completed_quarter" ?
											<BasicDD
												className="ml-auto"
												id={`${tab_id}-stacked_column_dd-vertical`}
												options={optionOverviewFilter}
												value={stackedColumnSelection}
												setValue={setStackedColumnSelection}
											/> : null
									}
								</div>
								<div style={{ height: "400px" }}>
									<StackedColumnChart
										id={`${tab_id}-stacked_column_chart-segment`}
										hiddenValueAxis={false}
										isOneHundredPercent={false}
										isPercentage={false}
										largerWidth={true}
										hideLabelBullet={false}
										isOverviewChart={true}
										isGrowthPercentToggable={true}
										stackedColumnSelection={stackedColumnSelection}
										selectedRangeValue={selectedRangeValue}
										data={grossNetBkgBridgeTrends.data}
										legend={grossNetBkgBridgeTrends.legend}
									/>
								</div>
							</Card.Body>
						</Card>
					</Col>
					<Col sm={12} md={3} lg={3}>
						<Card className="Segment revenue">
							<Card.Body>
								<Card.Title>gross-net bkg waterfall</Card.Title>
								<Legend legend={grossNetBkgWaterfall.legend} />
								<div style={{ height: "450px" }}>
									<WaterFallChart
										id={`${tab_id}-watterfall_chart`}
										data={grossNetBkgWaterfall.data}
										legend={grossNetBkgWaterfall.legend}
									/>
								</div>
							</Card.Body>
						</Card>
					</Col>
				</Row>
				<Row className="contentDivider">
					<Col sm={12} md={6} lg={6}>
						<Card className="SegmentDetails revenue">
							<Card.Body>
								<Card.Title>net % of gross by segment</Card.Title>
								<CustomTable
									tableName='segment-details-vertical scrollable'
									data={netPercentOfGrossBySegment === undefined ? [] : netPercentOfGrossBySegment}
									defaultSorted={defaultSorted}
									columns={columns}
								/>
							</Card.Body>
						</Card>
					</Col>
					<Col sm={12} md={3} lg={3}>
						<Card className="RevenueByType revenue">
							<Card.Body>
								<Card.Title>gross net bkg bridge by segment</Card.Title>
								<div style={{ height: "300px" }}>
									<StackedColumnChart
										id={`${tab_id}-stacked_column_chart-vertical`}
										hiddenValueAxis={false}
										isOneHundredPercent={false}
										isPercentage={false}
										largerWidth={true}
										hideLabelBullet={false}
										isOverviewChart={true}
										isGrowthPercentToggable={true}
										stackedColumnSelection={stackedColumnSelection}
										selectedRangeValue={selectedRangeValue}
										data={grossNetBkgBrideBySegment.data}
										legend={grossNetBkgBrideBySegment.legend}
									/>
								</div>
							</Card.Body>
						</Card>
					</Col>
					<Col sm={12} md={3} lg={3}>
						<Card className="revenue">
							<Card.Body>
								<Card.Title>net % of gross trends</Card.Title>
								<Legend legend={netPercentOfGrossTrends.legend} />
								<div style={{ height: "300px" }}>
									<LineChart
										id={`${tab_id}-line_chart-trends`}
										data={netPercentOfGrossTrends.data}
										legend={netPercentOfGrossTrends.legend}
									/>
								</div>
							</Card.Body>
						</Card>
					</Col>
				</Row>
			</>
	);
};

export default OverviewContentType2;