import React, { useState, useRef, useEffect } from 'react';
import * as ReactDOM from 'react-dom';
import SampleBase from '../../components/playground/sample-base/';
import SampleLayout from '../../components/playground/sample-layout/';
import DashboardLayout from '../../components/playground/sample-layout/dashboardLayout';
import TreeGrid from '../../components/playground/sample-treegrid/sampleTreeGrid';
import './executiveCockpit.scss';

import { useWidowResize } from '../../constants/utilityHooks';
import axios from 'axios';

//common components
import SortableComponent from './SortableComponent';
import Loader from '../../components/loader/Loader';
// constant
import { API_BASE_URL } from '../../constants/config';

const Content = (props) => {
  let [test, setTestItems] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const fetchPretty = async () => {
    setIsLoading(true)
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/executive-cockpit.json`,
    }).then(result => {
      if (result.data) {
        setTestItems(result.data);
        setIsLoading(false)
      }
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchPretty();
  }, []);

  console.log('test:', test);
  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);
  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;
  return (
    <div>
      {/* <SortableComponent /> */}
      {/* <SampleLayout data={test} /> */}
      {/* <TreeGrid /> */}
      <DashboardLayout data={test} />
    </div>

  )

};

export default Content;


