import React from 'react';
import { ButtonGroup, Button } from 'react-bootstrap';
import './filter.scss';

const FilterContainer = ({ children, className, onShowFilter, onClearFilter, onShowComment, showComment, showAddMetric, setModalShow }) => {
  console.log('executiveCockpit: ', showAddMetric)
  // clear filters
  const onClear = () => {
    window.location.reload()
  }

  return (
    <div className={`filter-container ${className ? className : ''}`}>

      {children}

      <ButtonGroup className={`ml-auto right-group ${showComment ? 'comment-open' : ''}`}>
        <Button variant="link" className="btn-all-filters" onClick={onShowFilter}>
          All Filters
        </Button>
        <Button variant="link" className="btn-clear" onClick={onClearFilter ? onClearFilter : onClear}>
          Clear
        </Button>
        {showAddMetric === true ?
          <Button variant="link" className="btn" onClick={() => {
            setModalShow(true);
          }}>
            Add Metric
        </Button> : null}
        <Button variant="link" className="btn-comments" onClick={onShowComment}>
          Comments
        </Button>

      </ButtonGroup>
    </div >
  );
}

export default FilterContainer;
