import React, { useState, useRef, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { renderToString } from 'react-dom/server'
import { DashboardLayoutComponent, PanelsDirective, PanelDirective } from '@syncfusion/ej2-react-layouts';
import { Row, Col, Card, Button, DropdownButton, Dropdown } from 'react-bootstrap';
import KPI_NEW from '../../../components/kpi/KPI_NEW';
import Legend from '../../../components/legend/Legend';
import StackedColumnChart from '../../../components/chart/StackedColumnChart';
import { BiExpand } from "react-icons/bi";
import { RiDragMove2Line, RiDeleteBin6Line } from "react-icons/ri";
import { FiMoreVertical } from "react-icons/fi";

import "./dashboardLayout.scss";



const DashboardLayout = (props) => {
  let history = useHistory();
  var dashboardObj = useRef(null);
  const { data } = props;
  const goTo = (url) => {

    history.push('/executive-cockpit' + url);
  }

  const doubleClick = (event, url) => {
    goTo(url)
  }


  const smallCard = (item, panelId, size) => {
    console.log('smallCard')
    let availableSizes = item.layout.availableSizes;
    return (
      <Card.Body onDoubleClick={(e) => { doubleClick(e, item.url) }}>
        <Card.Title> {item.title}</Card.Title>
        <div className="button-container">
          <DropdownButton id="dropdown-basic-button" className="btn-gray pull-left" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
            <Dropdown.Item onClick={() => { alert('Are you sure you want to delete card?') }}>Delete Card</Dropdown.Item>
            {availableSizes.map(element => {
              return (
                <>

                  {size ?
                    <>
                      {size !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                    :
                    <>
                      {item.layout.defaultSize !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                  }
                </>
              )
            })}
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
        </div>

        <div style={{ height: "120px" }}>
          <StackedColumnChart
            id={'executive-stacked-' + item._id}
            hiddenValueAxis={true}
            isOneHundredPercent={false}
            isPercentage={false}
            largerWidth={true}
            data={item.chart.data}
            legend={item.chart.legend}
            hideLabelBullet={true}
            disablePaddingTop={true}
          />
        </div>
      </Card.Body>
    )

  }

  const mediumCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    return (
      <Card.Body onDoubleClick={(e) => { doubleClick(e, item.url) }}>
        <Card.Title> {item.title}</Card.Title>
        <div className="button-container">
          <DropdownButton id="dropdown-basic-button" className="btn-gray pull-left" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>
            <Dropdown.Item onClick={() => { alert('Are you sure you want to delete card?') }}>Delete Card</Dropdown.Item>
            {availableSizes.map(element => {
              return (
                <>
                  {size ?
                    <>
                      {size !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                    :
                    <>
                      {item.layout.defaultSize !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                  }
                </>
              )
            })}
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
        </div>
        <Legend legend={item.chart.legend} />
        <div style={{ height: "350px" }}>
          <StackedColumnChart
            id={'executive-stacked-' + item._id}
            hiddenValueAxis={true}
            isOneHundredPercent={false}
            isPercentage={false}
            largerWidth={true}
            data={item.chart.data}
            legend={item.chart.legend}
            hideLabelBullet={true}
          />
        </div>
      </Card.Body>
    )
  }
  const largeCard = (item, panelId, size) => {
    let availableSizes = item.layout.availableSizes;
    return (
      <Card.Body onDoubleClick={(e) => { doubleClick(e, item.url) }}>
        <Card.Title> {item.title}</Card.Title>
        <div className="button-container">
          <DropdownButton id="dropdown-basic-button" drop={'left'} className="btn-gray" title={<FiMoreVertical />}>
            <Dropdown.Item onClick={() => { goTo(item.url) }}>View Metric</Dropdown.Item>

            <Dropdown.Item onClick={() => { alert('Are you sure you want to delete card?') }}>Delete Card</Dropdown.Item>
            {availableSizes.map(element => {
              return (
                <>
                  {size ?
                    <>
                      {size !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                    :
                    <>
                      {item.layout.defaultSize !== element.size ?
                        <Dropdown.Item onClick={() => {
                          onResize(panelId, element.sizeX, element.sizeY, element.size, item)
                        }}>Resize to {element.size}</Dropdown.Item>
                        : null}
                    </>
                  }

                </>
              )
            })}
            <Dropdown.Item onClick={() => { alert('You clicked details.') }}>Details</Dropdown.Item>
          </DropdownButton>
          {/* <Button className="btn-gray"><RiDeleteBin6Line /></Button> */}
          {/* <Button className="btn-gray btn-expand" onClick={() => goTo(item.url)}><BiExpand /></Button> */}
        </div>
        <Row>
          <Col>
            <KPI_NEW
              totalValue={item.overall.total.value}
              totalUnit={item.overall.total.unit}
              details={item.overall.details}
            />
          </Col>
          <Col>
            <Legend legend={item.chart.legend} />
            <div style={{ height: "350px" }}>
              <StackedColumnChart
                id={'executive-stacked-' + item._id}
                hiddenValueAxis={true}
                isOneHundredPercent={false}
                isPercentage={false}
                largerWidth={true}
                data={item.chart.data}
                legend={item.chart.legend}
                hideLabelBullet={true}
              />
            </div>
          </Col>
        </Row>
      </Card.Body>
    )
  }


  const onResize = (id, sizeX, sizeY, size, item) => {
    dashboardObj.resizePanel(id, sizeX, sizeY)
    let panelContent;
    if (size === 'S') {
      panelContent = smallCard(item, id, size)
    } else if (size === 'M') {
      panelContent = mediumCard(item, id, size)
    } else if (size === 'L') {
      panelContent = largeCard(item, id, size)
    }
    dashboardObj.updatePanel({
      id: id,
      content: () => {
        return (
          panelContent
        )

      }
    })
    console.log(JSON.stringify(dashboardObj.serialize()))
  }


  return (
    <div>
      <DashboardLayoutComponent
        id="executive-cockpit"
        columns={6}
        allowResizing={false}
        allowDragging={true}
        cellAspectRatio={100 / 80}
        cellSpacing={[30, 30]}
        mediaQuery={"max-width: 700px"}
        ref={(scope) => { dashboardObj = scope; }}
      >
        <PanelsDirective>
          {data.map((item, i) => {
            let size, sizeX, sizeY;

            size = item.layout.defaultSize;
            sizeX = item.layout.defaultSizeX;
            sizeY = item.layout.defaultSizeY;
            return (
              <PanelDirective
                key={i}
                id={'panel-' + i}
                sizeX={size === 'S' ? 1 : size === 'M' ? 1 : size === 'L' ? 2 : null}
                sizeY={size === 'S' ? 1 : size === 'M' ? 2 : size === 'L' ? 2 : null}
                row={item.layout.row}
                col={item.layout.col}
                content={() => {
                  let panelContent;
                  if (size === 'S') {
                    panelContent = smallCard(item, 'panel-' + i)
                  } else if (size === 'M') {
                    panelContent = mediumCard(item, 'panel-' + i)
                  } else if (size === 'L') {
                    panelContent = largeCard(item, 'panel-' + i)
                  } else if (sizeX === 1 && sizeY === 1) {
                    panelContent = smallCard(item, 'panel-' + i)
                  }
                  return (
                    panelContent
                  )
                }
                }
              />
            )


          })}
        </PanelsDirective>
      </DashboardLayoutComponent>

    </div>

  )

};

export default DashboardLayout;