import React, { useState } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import Legend from '../../components/legend/Legend';
import BasicDD from '../../components/customDropdown/BasicDD';
import Kpi from '../../components/kpi/Kpi';
import CustomTable from '../../components/customTable/CustomTable';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import DonutChart from '../../components/chart/DonutChart';
import LineChart from '../../components/chart/LineChart';
import KPI_NEW from '../../components/kpi/KPI_NEW';

import { EMPTY_INITIAL, optionOverviewFilter } from '../../constants/filterOptions';
import { convertToUSDGrowth, convertToPercent } from '../../constants/formatter';


const OverviewContent = (props) => {
  const { overviewData, selectedRangeValue } = props;

  const stacked_column_initial_value = optionOverviewFilter ? optionOverviewFilter[0].key : EMPTY_INITIAL;
  const [stackedColumnSelection, setStackedColumnSelection] = useState(stacked_column_initial_value);

  //by segment
  const overallSegment = overviewData ? overviewData.overall : [];
  const grossBookingsByVerticalSegment = overviewData ? overviewData.gross_bookings_by_segment ? overviewData.gross_bookings_by_segment : overviewData.gross_bookings_by_segment : [];
  const segmentTrendsSegment = overviewData ? overviewData.segment_trends_by_segment ? overviewData.segment_trends_by_segment : overviewData.segment_trends_by_segment : [];
  const segmentRevenueMixSegment = overviewData ? overviewData.segment_revenue_mix : [];
  const segmentDetailsSegment = overviewData ? overviewData.segment_details : [];

  const indicatorPercent = (cell, row) => {
    let bgColor = cell >= 0.5 ? 'indicator-up' : cell <= -0.5 ? 'indicator-down' : 'indicator-middle';
    let icon = cell >= 0.5 ? <ImArrowUpRight2 /> : cell <= -0.5 ? <ImArrowDownRight2 /> : <span className="mr-3"></span>;

    return (
      <>
        <span className={`indicator ${bgColor}`}>
          {icon}
          {convertToPercent(cell)}
        </span>
      </>
    );
  };

  const formatUSDGrowth = (cell, row) => {
    return (
      <span>
        {convertToUSDGrowth(cell)}
      </span>
    );
  };

  const formatToPercent = (cell, row) => {
    return (
      <span>
        {convertToPercent(cell)}
      </span>
    );
  };

  const defaultSorted = [
    {
      dataField: 'mix_percent',
      order: 'desc', // or asc
    }
  ];

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment-th',
      headerClasses: 'segment-td'
    },
    {
      dataField: 'revenue',
      text: 'MRC $',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'mix_percent',
      text: 'MRR MIX%',
      sort: true,
      formatter: formatToPercent,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_to_q_growth_usd',
      text: 'Q-Q $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorPercent,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_growth_usd',
      text: 'Y-Y $ GROWTH',
      sort: true,
      formatter: formatUSDGrowth,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_mpq',
      text: '%BUDGET',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_budget',
      text: '%MPQ',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ];

  return (
    <Container className="overview" fluid>
      <Row className="contentDivider" style={{ paddingBottom: "30px" }}>
        <div className="fix-width">
          <Card className="TotalRevenue revenue" >
            <Card.Body>
              <Card.Title>gross bookings</Card.Title>
              <KPI_NEW
                totalValue={overallSegment.total.value}
                totalUnit={overallSegment.total.unit}
                details={overallSegment.details}
              />
              {/* <Kpi
                data={overall_segment}
                total={overall_segment.total}
                withPercent={true}
              /> */}
            </Card.Body>
          </Card>
        </div>
        <Col>
          <Card className="RevenueByType revenue">
            <Card.Body>
              <Card.Title>gross bookings by vertical</Card.Title>
              <div className="d-flex">
                <Legend legend={grossBookingsByVerticalSegment.legend} />
                {
                  selectedRangeValue === "last_completed_quarter" ?
                    <BasicDD
                      className=" ml-auto"
                      id="overviewStackedColumn"
                      options={optionOverviewFilter}
                      value={stackedColumnSelection}
                      setValue={setStackedColumnSelection}
                    /> : null
                }
              </div>
              <div style={{ height: "400px" }}>
                <StackedColumnChart
                  id="customer_revenue_by_revenue_type_stacked_column_chart"
                  hiddenValueAxis={false}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  largerWidth={true}
                  hideLabelBullet={false}
                  isOverviewChart={true}
                  isGrowthPercentToggable={true}
                  stackedColumnSelection={stackedColumnSelection}
                  selectedRangeValue={selectedRangeValue}
                  data={grossBookingsByVerticalSegment.data}
                  legend={grossBookingsByVerticalSegment.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="Segment revenue">
            <Card.Body>
              <Card.Title>segment mrr mix</Card.Title>
              <Legend legend={segmentRevenueMixSegment.legend} />
              <div style={{ height: "350px" }}>
                <DonutChart
                  id={"customer_revenue_overview_donut_chart"}
                  totalTitle={"GROSS BOOKINGS"}
                  total={segmentRevenueMixSegment.revenue}
                  data={segmentRevenueMixSegment.data}
                  legend={segmentRevenueMixSegment.legend}
                  isDollar={true}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider">
        <Col>
          <Card className="SegmentDetails revenue">
            <Card.Body>
              <Card.Title>segment details</Card.Title>
              <CustomTable
                tableName='segment-details scrollable'
                data={segmentDetailsSegment === undefined ? [] : segmentDetailsSegment}
                defaultSorted={defaultSorted}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
        <Col style={{ maxWidth: "400px" }}>
          <Card className="SegmentTrends revenue">
            <Card.Body>
              <Card.Title>segment y/y growth trends</Card.Title>
              <Legend legend={segmentTrendsSegment.legend} />
              <div style={{ height: "300px" }}>
                <LineChart
                  id={"customer_revenue_overview_line_chart"}
                  data={segmentTrendsSegment.data}
                  legend={segmentTrendsSegment.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default OverviewContent;